@charset "UTF-8";
/* Metronet Profile Picture */
#metronet-profile-image {
  position: relative;
  float: left;
}

#metronet-profile-image a.mpp_add_media #metronet-click-edit,
#metronet-profile-image a.mpp_add_media:hover #metronet-click-edit,
#metronet-profile-image a.mpp_add_media:visited #metronet-click-edit {
  color: #FFF;
}

#metronet-profile-image a.mpp_add_media:hover #metronet-click-edit {
  background: #000;
  background: #333333;
  font-weight: normal;
}

#metronet-click-edit {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #333;
  background: rgba(51, 51, 51, 0.5);
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  padding: 8px 0;
}

#metronet-remove {
  position: absolute;
  background: #424242;
  top: 0;
  right: 0;
  display: block;
  padding: 3px;
  width: 20px;
  height: 20px;
  overflow: hidden;
}

#metronet-remove:before {
  content: "ï†‚";
  color: #fd6a6a;
  font-size: 20px;
  margin-right: 20px;
}

#metronet-remove:hover:before {
  color: #ff3e3e;
}

#metronet-remove.mpp-no-profile-image {
  display: none;
}

#metronet-override-avatar {
  clear: left;
}

.username.column-username img {
  cursor: pointer;
}

.mpp_hide_wp_avatar .user-profile-picture {
  display: none;
}